import React from "react";
import { History, Clock } from "lucide-react";

interface RecentSearchesProps {
  searches: string[];
  onSelect: (location: string) => void;
}

export const RecentSearches = ({ searches, onSelect }: RecentSearchesProps) => {
  if (searches.length === 0) return null;

  return (
    <div className="w-full">
      <div className="flex items-center gap-2 mb-3 text-sm text-muted-foreground">
        <History className="w-4 h-4" />
        <span className="font-medium">Recent Locations</span>
      </div>

      <div className="flex flex-wrap gap-2">
        {searches.map((location, index) => (
          <button
            key={index}
            onClick={() => onSelect(location)}
            className="flex items-center gap-2 px-3 py-1.5 text-sm rounded-full bg-secondary text-secondary-foreground hover:bg-secondary/80 transition-colors"
          >
            <Clock className="w-3 h-3 opacity-50" />
            {location}
          </button>
        ))}
      </div>
    </div>
  );
};
