import React from "react";
import { Cloud, Droplets, Wind, Thermometer } from "lucide-react";

interface CurrentWeatherProps {
  data?: {
    temp: number;
    condition: string;
    high: number;
    low: number;
    feelsLike: number;
    humidity: number;
    windSpeed: number;
    location: string;
  };
  loading?: boolean;
}

export const CurrentWeather = ({ data, loading }: CurrentWeatherProps) => {
  if (loading) {
    return (
      <div className="w-full h-[400px] rounded-xl border bg-card text-card-foreground shadow-sm animate-pulse p-6">
        <div className="h-8 w-1/3 bg-muted rounded mb-4"></div>
        <div className="h-24 w-24 bg-muted rounded-full mb-4"></div>
        <div className="h-12 w-1/2 bg-muted rounded"></div>
      </div>
    );
  }

  if (!data) {
    return null;
  }

  return (
    <div className="w-full rounded-xl border bg-card text-card-foreground shadow-sm">
      <div className="p-6">
        <div className="flex flex-col md:flex-row justify-between items-center gap-6">
          {/* Main Temp & Condition */}
          <div className="flex flex-col items-center md:items-start">
            <h2 className="text-2xl font-bold text-muted-foreground">
              {data.location}
            </h2>
            <div className="flex items-center mt-2">
              <span className="text-7xl font-bold tracking-tighter">
                {Math.round(data.temp)}°
              </span>
              <Cloud className="w-16 h-16 ml-4 text-sky-500" />
            </div>
            <p className="text-xl text-muted-foreground mt-1 capitalize">
              {data.condition}
            </p>
            <div className="flex gap-4 mt-2 text-sm text-muted-foreground">
              <span>H: {Math.round(data.high)}°</span>
              <span>L: {Math.round(data.low)}°</span>
            </div>
          </div>

          {/* Details Grid */}
          <div className="grid grid-cols-2 gap-4 w-full md:w-auto">
            <div className="flex items-center gap-3 p-4 rounded-lg bg-muted/50">
              <Thermometer className="w-5 h-5 text-muted-foreground" />
              <div>
                <p className="text-sm font-medium">Feels Like</p>
                <p className="text-lg font-bold">
                  {Math.round(data.feelsLike)}°
                </p>
              </div>
            </div>

            <div className="flex items-center gap-3 p-4 rounded-lg bg-muted/50">
              <Droplets className="w-5 h-5 text-blue-500" />
              <div>
                <p className="text-sm font-medium">Humidity</p>
                <p className="text-lg font-bold">{data.humidity}%</p>
              </div>
            </div>

            <div className="flex items-center gap-3 p-4 rounded-lg bg-muted/50">
              <Wind className="w-5 h-5 text-slate-500" />
              <div>
                <p className="text-sm font-medium">Wind</p>
                <p className="text-lg font-bold">{data.windSpeed} mph</p>
              </div>
            </div>

            <div className="flex items-center gap-3 p-4 rounded-lg bg-muted/50">
              <Cloud className="w-5 h-5 text-gray-500" />
              <div>
                <p className="text-sm font-medium">UV Index</p>
                <p className="text-lg font-bold">Moderate</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
