import React from "react";
import { Cloud, Sun, CloudRain } from "lucide-react";

interface ForecastDay {
  date: string; // e.g., "Mon"
  temp_high: number;
  temp_low: number;
  condition: string;
}

interface ForecastProps {
  data?: ForecastDay[];
  loading?: boolean;
}

export const Forecast = ({ data, loading }: ForecastProps) => {
  if (loading) {
    return (
      <div className="w-full h-full min-h-[300px] rounded-xl border bg-card text-card-foreground shadow-sm animate-pulse p-6">
        <div className="h-6 w-1/3 bg-muted rounded mb-6"></div>
        <div className="space-y-4">
          {[1, 2, 3, 4, 5].map((i) => (
            <div key={i} className="h-12 w-full bg-muted rounded"></div>
          ))}
        </div>
      </div>
    );
  }

  if (!data) return null;

  return (
    <div className="w-full h-full rounded-xl border bg-card text-card-foreground shadow-sm">
      <div className="p-6">
        <h3 className="text-sm font-medium text-muted-foreground mb-4 uppercase tracking-wider">
          5-Day Forecast
        </h3>
        <div className="space-y-4">
          {data.map((day, index) => (
            <div
              key={index}
              className="flex items-center justify-between text-sm"
            >
              <span className="w-12 font-medium">{day.date}</span>

              <div className="flex items-center justify-center w-8">
                {/* Placeholder icon logic */}
                {day.condition.toLowerCase().includes("sun") ? (
                  <Sun className="w-5 h-5 text-yellow-500" />
                ) : day.condition.toLowerCase().includes("rain") ? (
                  <CloudRain className="w-5 h-5 text-blue-500" />
                ) : (
                  <Cloud className="w-5 h-5 text-gray-500" />
                )}
              </div>

              <div className="flex items-center gap-3 flex-1 ml-4">
                <span className="text-muted-foreground w-8 text-right">
                  {Math.round(day.temp_low)}°
                </span>

                {/* Visual Temperature Bar */}
                <div className="flex-1 h-1.5 bg-muted rounded-full overflow-hidden relative">
                  <div
                    className="absolute h-full bg-gradient-to-r from-blue-400 to-orange-400 rounded-full opacity-70"
                    style={{
                      left: "10%", // These would be dynamic in a real app based on min/max range
                      right: "10%",
                    }}
                  />
                </div>

                <span className="font-medium w-8 text-right">
                  {Math.round(day.temp_high)}°
                </span>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};
