import React, { useState } from "react";
import { Search, MapPin, Loader2, X } from "lucide-react";

interface SearchBarProps {
  onSearch: (location: string) => void;
}

export const SearchBar = ({ onSearch }: SearchBarProps) => {
  const [query, setQuery] = useState("");
  const [isOpen, setIsOpen] = useState(false);
  const [loading, setLoading] = useState(false);

  // Mock results for UI visualization
  const [results, setResults] = useState<string[]>([]);

  const handleSearch = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;
    setQuery(value);

    if (value.length > 2) {
      setLoading(true);
      setIsOpen(true);

      // Simulate API call
      setTimeout(() => {
        setResults([
          "London, United Kingdom",
          "London, Ontario, Canada",
          "Londonderry, Northern Ireland",
        ]);
        setLoading(false);
      }, 500);
    } else {
      setIsOpen(false);
    }
  };

  const handleSelect = (location: string) => {
    setQuery(location);
    setIsOpen(false);
    onSearch(location);
  };

  const clearSearch = () => {
    setQuery("");
    setIsOpen(false);
  };

  return (
    <div className="relative w-full max-w-xl mx-auto z-50">
      <div className="relative">
        <Search className="absolute left-3 top-1/2 -translate-y-1/2 w-5 h-5 text-muted-foreground" />

        <input
          type="text"
          className="w-full h-12 pl-10 pr-10 rounded-xl border border-input bg-background/50 backdrop-blur-sm text-foreground shadow-sm transition-all focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring"
          placeholder="Search city..."
          value={query}
          onChange={handleSearch}
          onFocus={() => query.length > 2 && setIsOpen(true)}
        />

        {loading ? (
          <Loader2 className="absolute right-3 top-1/2 -translate-y-1/2 w-5 h-5 animate-spin text-muted-foreground" />
        ) : query ? (
          <button
            onClick={clearSearch}
            className="absolute right-3 top-1/2 -translate-y-1/2 p-1 hover:bg-muted rounded-full"
          >
            <X className="w-4 h-4 text-muted-foreground" />
          </button>
        ) : null}
      </div>

      {isOpen && results.length > 0 && (
        <div className="absolute top-full left-0 right-0 mt-2 bg-popover text-popover-foreground rounded-xl border shadow-lg overflow-hidden animate-in fade-in zoom-in-95 duration-100">
          <div className="p-1">
            {results.map((result, index) => (
              <button
                key={index}
                onClick={() => handleSelect(result)}
                className="w-full flex items-center gap-3 px-4 py-3 text-sm rounded-lg hover:bg-accent hover:text-accent-foreground transition-colors text-left"
              >
                <MapPin className="w-4 h-4 text-muted-foreground" />
                <span>{result}</span>
              </button>
            ))}
          </div>
        </div>
      )}
    </div>
  );
};
